<template>
    <div class="login">
        <div class="login_main">
            <div class="login_header">
                <h2>宜宾换流站辅助监测平台</h2>
            </div>
            <div class="login_content">
                <div class="login_form">
                    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px"
                        class="demo-ruleForm">
                        <el-form-item prop="name">
                            <div class="el-icon-user logintitle">登录帐号</div>
                            <el-input v-model="ruleForm.name" placeholder="请输入登录帐号"></el-input>
                        </el-form-item>
                        <el-form-item prop="pwd">
                            <div class="el-icon-user logintitle">密码</div>
                            <el-input type="password" v-model="ruleForm.pwd" placeholder="请输入密码"></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="handleLogin('ruleForm')" class="btn">登录</el-button>
                        </el-form-item>
                    </el-form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import login from "./login";
export default login;
</script>
<style scoped>
@import url("./login.css");
/* @import url("../../assets/css/common.css"); */
</style>


