import {
  login,
} from '../../utils/service';
// import {
//   SmCrypto
// } from "sm-crypto";
// import smCrypto from "sm-crypto";
// const {
//   SmCrypto
// } = smCrypto;
export default {
  components: {},
  props: {},
  data() {
    return {
      ruleForm: {},
      rules: {
        name: [{
          required: true,
          message: '请输入用户名!',
          trigger: 'blur'
        }, ],
        region: [{
          required: true,
          message: '请输入密码!',
          trigger: 'change'
        }],
      }
    };
  },
  computed: {},
  methods: {
    GetPassWord(str) {
      var val = SmCrypto.doSm3AndSm2Encrypt(str) + "0000",
        ascCode = 0;
      for (var i = 0; i < str.length; i++) {
        ascCode = str.charCodeAt(i).toString(16);
        if (ascCode.length == 1)
          val += "00" + ascCode;
        else if (ascCode.length == 2)
          val += "0" + ascCode;
        else
          val += ascCode;
      }
      return val;
    },
    //点击登录按钮
    handleLogin() {
      this.GetPassWord(this.ruleForm.pwd)
      console.log('res :>> ', this.ruleForm.pwd);
      var formData = {
        login_account: this.ruleForm.name,
        pwd: this.ruleForm.pwd
      }
      login(formData, res => {
        console.log('res :>> ', res);
      });
      this.$store.dispatch("login");
      // localStorage.setItem('zyanUser', 1);
      // router.push({
      //   path: "/"
      // });
    },
  },
}



